















import Vue from "vue";

export default Vue.extend({
  data() {
    return {
      selected: "ALL",
      list: [
        "ALL",
        "HTTP",
        "SpringBoot",
        "Node.js",
        "Vue.js",
        "React.js",
        "ect",
      ],
    };
  },
  methods: {
    listClick(tapName:string) {
      this.selected = tapName;
      this.$emit('@selected', tapName);
    },
  },
});
