

























import Vue from "vue";

export default Vue.extend({
  props: {
    id: {
      type: Number,
    },
    title: {
      type: String,
    },
    date: {
      type: String,
      default: "00-00-00",
    },
    imgpath: {
      type: String,
    },
    writer: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      data: "data",
    };
  },
  methods: {
    getPost() {
      this.$router.push({ name: `Read`, params: { id: this.id.toString() } });
    },
  },
});
