





















import Vue from "vue";

export default Vue.extend({
  props: {
    pattern: {
      type: RegExp,
    },
    placeHolder: {
      type: String,
      default: "",
    },
    describedby: {
      type: String,
      default: "",
    },
    title: {
      type: String,
      default: "",
    },
    type: {
      type: String,
      default: "text",
    },
    enterSeubmit: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    state(): boolean | null {
      if (this.value.length === 0) return null;
      if (this.value.match(new RegExp(this.pattern))) {
        return true;
      } else {
        return false;
      }
    },
  },
  data() {
    return {
      value: "" as string,
    };
  },
  methods: {
    onKeyup(e: KeyboardEvent) {
      if (this.enterSeubmit && e.keyCode === 13) {
        this.$emit("@submit");
      }
      this.$emit("@keyup", this.value, this.state);
    },
    focusInput() {
      const inputRef = this.$refs.input as any;
      inputRef.focus();
    },
  },
});
