






import Vue from "vue";

export default Vue.extend({
  props: {
    show: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {};
  },
  methods: {
    offModal(e: { target: Element }): void {
      e.target.classList.contains("backdrop") ? this.$emit("@off", e) : null;
    },
  },
});
